@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
* {
  margin: 0;
  font-family: 'Quicksand', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: #242424;
    height:100vh;
}

#root {
    height: 100%;
    z-index: -1;
}

.home-container{display:flex;flex-direction:column}.rooms-header{display:flex;flex-direction:row}.rooms-header-text{display:flex;color:white;flex-direction:column;align-items:left;justify-content:center;padding-left:0}.add-btn{display:flex;align-items:center;flex-direction:column;color:white;justify-content:center;margin:0 0 0 .5rem;text-decoration:none}.add-btn:hover{color:#C4FF4D;cursor:pointer}.rotate-icon{transform:rotate(45deg)}.search-window-container{display:flex;justify-content:center;align-items:center}

.friend{display:flex;flex-direction:column;align-items:center;justify-content:center}.img-large{display:flex;border-radius:.4rem}.img-small{display:flex;border-radius:.4rem;width:50%;height:50%;margin:0 auto}.friend-img{width:100%;height:auto}.friend-name{display:flex;color:white;align-items:center;justify-content:center}.friend-container{display:flex;position:relative;width:100%;align-items:center;justify-content:center}.overlay.img-large{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;opacity:0;transition:.3s ease;background-color:#242424}.friend-container:hover .overlay{opacity:.75}.icon{color:white;font-size:100px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);text-align:center}.fa-user:hover{color:#eee}.select-friend{height:2.5rem;background-color:#884DFF;margin-top:3rem}.leave-btn{margin-top:6rem}.invite-friend-label{text:white;margin-top:2rem;z-index:100}.overlay-btn{display:flex;min-width:75%;margin:0 auto}

.navbar{height:4.5rem;display:flex;align-items:center;background:#884dff;color:white;justify-content:right;padding:0 2rem}.nav-bars,.nav-x{color:white;background:none}.nav-bars:hover,.nav-x:hover{color:#C4FF4D;cursor:pointer}.nav-x{margin:0 auto}.nav-toggle{display:flex;justify-content:flex-start;align-items:center;margin:1rem}.nav-menu{background:#884dff;width:15rem;height:100vh;display:flex;justify-content:center;top:0;position:fixed;left:-100%;transition:600ms;z-index:200}.nav-menu.active{left:0;transition:350ms;z-index:200}.nav-text{display:flex;justify-content:flex-start;align-items:center;list-style:none;padding:1rem 0;margin:0 2rem}.nav-text a{text-decoration:none;color:white;font-size:1.4rem;border-radius:.3rem;margin:0 auto}.nav-text a:hover{background-color:#C4FF4D;color:#242424}.nav-friend{display:flex;justify-content:flex-start;align-items:center;list-style:none;margin:0 2rem}.nav-friend a{text-decoration:none;color:white;font-size:1.5rem;border-radius:.3rem;height:8rem}.nav-friend a:hover{background-color:#C4FF4D;color:#242424}.nav-menu-items{width:100%;list-style:none}.navbar-toggle{background-color:#884dff;display:flex;justify-content:flex-start;align-items:center;list-style:none}

:root{--primary:   #884DFF;--secondary: #C4FF4D}.btn{border-radius:.3rem;outline:none;border:none;cursor:pointer}.btn--primary{background-color:#884DFF;background-color:var(--primary);color:white;border:1px solid #884DFF;border:1px solid var(--primary)}.btn--secondary{background-color:#C4FF4D;background-color:var(--secondary);color:#242424;border:1px solid #C4FF4D;border:1px solid var(--secondary)}.btn--outline{background-color:transparent;color:white;padding:.5rem 1rem;border:1px solid #884DFF;border:1px solid var(--primary)}.btn--sm{padding:0;height:25%;margin:.25rem auto;font-size:1rem}.btn--med{padding:.5rem 1rem;font-size:1rem}.btn--lg{padding:.5rem 2rem;font-size:1rem}.overlay-btn{position:absolute;display:flex;flex-direction:row}

.search-window{display:flex;flex-direction:row;justify-content:center;position:absolute;width:90vw;border-radius:0.3rem;background:#884DFF;height:50vh;top:-100%;transition:600ms;z-index:100}.search-window.active{top:12vh;padding:0;min-width:90vw;min-height:50vw;transition:350ms;z-index:100}.search-box{width:100%}.found-user{color:white}.search-result{display:flex;list-style:none;justify-content:center;margin-top:1rem;width:auto}

.message{border-radius:.3rem;width:100%;min-height:5rem;padding:0}.not-current-user{border:1px solid #c4ff4d}.current-user{border:1px solid #884DFF}.message-text{color:white}

.create-window{display:flex;flex-direction:row;justify-content:center;position:absolute;width:90vw;border-radius:0.3rem;background:#884DFF;height:50vh;top:-100%;transition:600ms;z-index:100}.create-window.active{top:38vh;padding:0;min-width:90vw;min-height:50vw;transition:350ms;z-index:100}.search-box{width:100%}.found-user{color:white}.search-result{display:flex;list-style:none;justify-content:center;margin-top:1rem;width:auto}

.login-form{display:flex;flex-direction:column;align-items:center;height:100%;padding-top:10rem;padding-bottom:27rem}.signin-header{display:flex;color:white;flex-direction:row}.login-input{display:flex;flex-direction:row;width:100%;max-width:330px;padding:15px;margin:0 auto}.form-signin input[type="email"]{margin-bottom:-1px;border-bottom-right-radius:0;border-bottom-left-radius:0}.form-signin input[type="password"]{margin-bottom:10px;border-top-left-radius:0;border-top-right-radius:0}

.message-input{background-color:white;border-radius:.3rem;width:100%;min-height:3rem;padding:0}.textfield-container{margin:auto 0}

.room-friend-container{position:relative;height:92%;z-index:0}

